*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.3;
  font-family: sans-serif;
  color: #0e0e0e;
  background-color: #f2f2f2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #0e0e0e;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 3rem; }
  @media (max-width: 767px) {
    .container {
      padding: 0 1rem; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.title {
  font: 700 3rem/1.3 sans-serif;
  margin: 0 0 3rem; }

.list {
  padding: 0;
  list-style: none;
  margin: 0 -15px;
  display: flex;
  flex-wrap: wrap; }

.list__item {
  padding: 0 15px;
  width: 25%; }
  @media (max-width: 1200px) {
    .list__item {
      width: 33.333%; } }
  @media (max-width: 767px) {
    .list__item {
      width: 50%; } }
  @media (max-width: 420px) {
    .list__item {
      width: 100%; } }
  .list__item.errorMessage {
    width: 100%; }

.card {
  margin-bottom: 2rem; }

.card__image {
  width: 150px;
  height: 150px;
  object-fit: cover; }

.card__title {
  margin: 1rem 0 0; }
